.info-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
}

.info-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    border: 1px solid var(--lightgray);
    border-radius: 10px;
    background-color: var(--cream);
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.info-item:hover {
    background-color: var(--white);
}

.info-icon,
.info-action-icon {
    font-size: 18px;
    color: var(--darkFont75);
}

.info-icon {
    margin-right: 10px;
}

.custom-alert {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 16px;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
    z-index: 1000;
}

.custom-alert.hide {
    opacity: 0;
}
